.hero {
  min-height: 70vh;
  background-color: white;
  position: relative;
  overflow: hidden;
}

.hero img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero > div:nth-of-type(1) {
  position: absolute;
  right: 0px;
  top: -50%;
  width: 70%;
  height: 120%;
  -webkit-transform: rotate(30deg);
  -moz-transform: rotate(30deg);
  -ms-transform: rotate(30deg);
  -o-transform: rotate(30deg);
  transform: rotate(30deg);
  background-color: orange;
  border-radius: 15%;
}

.hero > div:nth-of-type(2) {
  position: absolute;
  width: 250px;
  height: 400px;
  /* background-color: yellow; */
  top: 15%;
  right: 30%;
  border-radius: 1em;
  z-index: 2;
}

.hero > div:nth-of-type(2) img {
  border-radius: 1em;
}

.hero > div:nth-of-type(3) {
  position: absolute;
  width: 150px;
  height: 200px;
  /* background-color: aqua; */
  top: 50%;
  right: 5%;
  border-radius: 3em 0em 3em 0em;
  z-index: 2;
}

.hero > div:nth-of-type(3) img {
  border-radius: 3em 0em 3em 0em;
}

.hero > div > h2 {
  color: orange;
  text-shadow: 1px 1px 0.5px white;
}

.hero div:nth-last-of-type(1) {
  position: relative;
  padding-top: 20%;
  margin-left: 5em;
  padding-bottom: 10%;
  z-index: 4;
}

@media screen and (max-width: 1000px) {
  /*.hero{
      }*/

  .hero div:nth-last-of-type(1) {
    margin-top: 50%;
    margin-left: 1em;
  }

  .hero > div:nth-of-type(1) {
    display: none;
  }

  .hero > div:nth-of-type(2) {
    top: 8%;
  }

  .hero > div:nth-of-type(3) {
    top: 8%;
    right: 5%;
    height: 300px;
  }

  .hero p {
    margin-top: 5%;
    width: 60%;
  }
}

@media screen and (max-width: 700px) {
  .hero {
    background-color: white;
    background: none;
  }

  .hero > div:nth-of-type(2),
  .hero > div:nth-of-type(1) {
    display: none;
  }
}
