#imagecontainer {
  margin-top: 1em;
  border-radius: 50%;
  background-color: aquamarine;
  width: 100px;
  height: 100px;
}

#profile input,
#profile input:focus {
  border: none;
  border-bottom: 1px solid black;
  width: 90%;
  height: 2em;
}
#profile > div:nth-of-type(1) {
  margin-left: 5%;
}

#profile > div:nth-of-type(2) {
  margin-left: 5%;
}

#profile > div:nth-of-type(2) input {
  border: 1px solid orange;
  width: 1em;
  transform: translate(0, -10%);
  margin-left: 1.5em;
}
#profile > div:nth-of-type(2) div {
  display: block;
  flex-wrap: wrap;
}
#profile > div:nth-of-type(2) div > select {
  margin: 0 0.4em;
  min-width: 40px;
  border-radius: 0.4em;
  padding: 0.5em;
}

#profile button {
  float: right;
  background-color: black;
  padding: 1em 2em;
  margin: 3em;
  border-radius: 3em;
  color: white;
}
