table {
  margin-top: 3em;
  width: 90%;
  border-collapse: collapse;
  text-align: center;
  margin-bottom: 2em;
}

thead td {
  border-bottom: 1px solid #000000;
  background-color: black;
  color: white;
  padding: 0.3em;
}
tbody td {
  padding-top: 0.8em;
}

@media screen and (max-width: 1000px) {
  .req_service {
    overflow-y: scroll;
    padding: 0.5em;
  }
}

table button {
  background-color: transparent;
  color: red;
  border: none;
}
