#loginpage button,
#loginpage input,
#forgotPassword button,
#forgotPassword input {
  border-radius: 1em;
  min-width: 70%;
  height: 2.5em;
}

#loginpage input {
  border: 0.1em solid rgb(106, 82, 82);
}

#loginpage button,
#forgotPassword button {
  background-color: orange;
  border: none;
}

#loginpage > * {
  margin-bottom: 1.5em;
}

#loginpage > div span {
  margin-right: 2em;
}

#loginpage > div span input {
  max-width: 0.5em;
}

@media screen and (max-width: 700px) {
  #loginpage > center:nth-of-type(1) {
    display: none;
  }
}

#loginpage p,
#forgotPassword p,
#updatepassword p {
  display: block;
  color: red;
  margin-top: -1em;
}

#loginpage a:hover {
  text-decoration: underline;
}
