#servicepage {
  padding: 5%;
  min-height: 50vh;
  display: flex;
  flex-wrap: wrap;
  /*overflow: scroll;*/
}

#servicepage > div {
  min-width: 50%;
  min-height: 100%;
}

#servicepage > div:nth-of-type(1) div {
  background-color: green;
  width: 70%;
  height: 300px;
  border-radius: 20px;
  margin-bottom: 0em;
}

#servicepage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: inherit;
}

#servicepage button,
#backButton,
#paymentPrompt button {
  margin-top: 1.5em;
  background-color: orange;
  padding: 1em 2em;
  border-radius: 1.2em;
  border: none;
  color: white;
  min-width: 200px;
  text-align: left;
}

#backButton {
  border-radius: 0px;
  background-color: black;
  text-align: center;
}

#servicepage > div:nth-of-type(1) button {
  background-color: red;
}

@media screen and (max-width: 1000px) {
  #servicepage div {
    width: 100%;
  }

  #servicepage > div:nth-of-type(1) div {
    width: 100%;
  }
  #servicepage > div:nth-of-type(2) {
    padding-top: 2em;
  }
}

#paymentPrompt {
  margin: 5em 0em;
}

#paymentPrompt button:nth-of-type(1) {
  background-color: red;
  margin-right: 2em;
}

#make_payments {
  padding-bottom: 2em;
}

#make_payments button {
  margin-top: 1.5em;
  background-color: orange;
  padding: 1em 2em;
  border-radius: 0em;
  border: none;
  color: white;
  min-width: 200px;
  text-align: left;
}
