.happiness {
  min-height: 50vh;
  display: flex;
  flex-wrap: wrap;
}

.happiness > div:nth-of-type(1) {
  /*margin: 3em 0em 0em 3em;*/
  position: relative;
  width: 60%;
  min-height: 650px;
}

.happiness > div:nth-of-type(2) {
  margin-top: 150px;
  text-align: right;
  width: 30%;
  margin-right: 10%;
  min-height: 100%;
  line-height: 1.6em;
}

.happiness > div:nth-of-type(1) div:nth-of-type(1) {
  position: absolute;
  right: 50%;
  top: 50%;
  transform: translate(100%, -100%);
  -webkit-transform: translate(100%, -100%);
  -moz-transform: translate(100%, -100%);
  -o-transform: translate(100%, -100%);
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background-color: orange;
}

.happiness > div:nth-of-type(1) div:nth-of-type(2) {
  position: absolute;
  right: 50%;
  top: 50%;
  transform: translate(50%, -50%);
  -webkit-transform: translate(50%, -50%);
  -moz-transform: translate(50%, -50%);
  -o-transform: translate(50%, -50%);
  width: 300px;
  height: 350px;
  border-radius: 45%;
  /*background-color: blueviolet;*/
  z-index: 2;
}

.happiness img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 45%;
}

.happiness > div:nth-of-type(1) div:nth-of-type(3) {
  position: absolute;
  right: 50%;
  top: 50%;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background-color: black;
  border: 1px solid white;
  z-index: 1;
}

.happiness button {
  background-color: orange;
  border-radius: 10px;
  border: none;
  padding: 1em 3em;
}

@media screen and (max-width: 1000px) {
  .happiness > div:nth-of-type(1) {
    margin: 1em 1em 1em 1em;
    width: 100%;
  }

  .happiness > div:nth-of-type(2) {
    width: 100%;
    text-align: center;
    padding-right: 0em;
    margin-top: 20px;
    margin-right: 0%;
  }
}

@media screen and (max-width: 650px) {
  .happiness > div:nth-of-type(1) {
    min-height: 80vw;
  }

  .happiness > div:nth-of-type(1) div:nth-of-type(1) {
    width: 40vw;
    height: 40vw;
  }

  .happiness > div:nth-of-type(1) div:nth-of-type(2) {
    width: 40vw;
    height: 40vw;
  }

  .happiness > div:nth-of-type(1) div:nth-of-type(3) {
    width: 40vw;
    height: 40vw;
  }
}
